/* eslint-disable no-console */
import React from 'react';
import './App.css';
import { GoodsList } from './components/GoodsList';

const goodsFromServer: string[] = [
  'Dumplings',
  'Carrot',
  'Eggs',
  'Ice cream',
  'Apple',
  'Bread',
  'Fish',
  'Honey',
  'Jam',
  'Garlic',
];

interface State {
  goods: string[];
  showList: boolean;
  value: string;
}

class App extends React.Component<{}, State> {
  state: State = {
    goods: [...goodsFromServer],
    showList: false,
    value: `${goodsFromServer.length}`,
  };

  reverseMetod = () => {
    this.setState((state: State) => ({
      goods: [...state.goods].reverse(),
    }));
  };

  sortAlphabetically = () => {
    this.setState((state: State) => ({
      goods: state.goods.sort((a: string, b: string) => {
        return a.localeCompare(b);
      }),
    }));
  };

  sortByLength = () => {
    this.setState((state: State) => ({
      goods: state.goods.sort((a: string, b: string) => {
        return a.length - b.length;
      }),
    }));
  };

  render() {
    const {
      goods, showList,
    } = this.state;

    console.log(this.state.value);

    return (
      <div className="App">
        <h1>Goods</h1>
        {!showList
          ? (
            <button
              type="button"
              onClick={() => {
                this.setState((state: State) => ({ showList: !state.showList }));
              }}
            >
              Start
            </button>
          )
          : (
            <GoodsList
              props={goods.filter((_item, i) => i < +this.state.value)}
            />
          )}
        <button
          type="button"
          disabled={!showList}
          onClick={() => {
            this.reverseMetod();
          }}
        >
          Reverse
        </button>

        <button
          type="button"
          disabled={!showList}
          onClick={() => {
            this.sortAlphabetically();
          }}
        >
          Sort alphabetically
        </button>

        <button
          type="button"
          disabled={!showList}
          onClick={() => {
            this.sortByLength();
          }}
        >
          Sort by length
        </button>

        <button
          type="button"
          disabled={!showList}
          onClick={() => {
            this.setState({
              goods: [...goodsFromServer],
              value: `${goodsFromServer.length}`,
            });
          }}
        >
          Reset
        </button>

        <select
          value={this.state.value}
          onChange={(e) => this.setState({ value: e.currentTarget.value })}
        >
          {goodsFromServer.map((_item, i) => {
            return (
              <option
                value={i + 1}
              >
                {i + 1}
              </option>
            );
          })}
        </select>

      </div>
    );
  }
}

export default App;
